import { Brain, Smartphone, Cog } from "lucide-react";
import SKILLS_DATA from "@/database/skills.json";

const ICON_MAP: Record<string, any> = {
    Brain,
    Smartphone,
    Cog,
};

export default function TechAuthority() {
    return (
        <section id="stack" className="py-24 bg-black">
            <div className="mx-auto max-w-7xl px-6 lg:px-8">
                <div className="mx-auto max-w-2xl text-center mb-16">
                    <h2 className="text-brand-amber font-semibold tracking-wide uppercase text-sm mb-4">Engineering Core</h2>
                    <p className="text-3xl font-bold tracking-tight text-white sm:text-5xl">
                        A Decade of Architectural Depth
                    </p>
                </div>

                <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
                    {SKILLS_DATA.map((pillar, idx) => {
                        const Icon = ICON_MAP[pillar.icon] || Brain;
                        return (
                            <div
                                key={idx}
                                className="glass-card flex flex-col items-start"
                            >
                                <div className="mb-6 flex h-12 w-12 items-center justify-center rounded-xl bg-brand-amber/10 text-brand-amber border border-brand-amber/20">
                                    <Icon size={24} />
                                </div>
                                <h3 className="text-xl font-bold text-white mb-3">
                                    {pillar.title}
                                </h3>
                                <p className="text-slate-400 text-sm leading-relaxed mb-6">
                                    {pillar.description}
                                </p>
                                <div className="mt-auto flex flex-wrap gap-2">
                                    {pillar.skills.map((skill, sIdx) => (
                                        <span
                                            key={sIdx}
                                            className="inline-flex items-center rounded-md bg-white/5 px-2 py-1 text-xs font-medium text-brand-amber ring-1 ring-inset ring-brand-amber/20"
                                        >
                                            {skill}
                                        </span>
                                    ))}
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
}
